@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Mulish:wght@400;900&family=Pacifico&family=Roboto:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Pacifico&family=Roboto:wght@400;500;700&display=swap');

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	font-family: 'Mulish', sans-serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block
}

html {
	height: 100%;
	background-color: #343a40;
	color: #c8c8c8;
}

body {
	line-height: 1;
	font-size: 100%;
}

ol,
ul {
	list-style: none
}

blockquote,
q {
	quotes: none
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none
}

table {
	border-collapse: collapse;
	border-spacing: 0
}
.App {
	display: flex;
	flex-direction: column;
}
.navbar-content-wrapper {
	margin-top: 1em;
	position: relative;
	
}

.navbar-content {
	display: flex;
	height: 100%;
}

.content-wrapper {
	background-color: #252525;
	width: 100%;
	min-height: 450px;
	display: flex;
	justify-content: center;
	padding: 2em;
	.content {
		width: 100%;
	}
		&.has-login .content {
			padding: 0;
		}
}
.wrapper-login {
	width: 100%;
	margin: 0 auto;
}

@media screen and (max-width: 1000px) {
	body {
		font-size: 90%;
	}
}
@media screen and (max-width: 860px) {
	body {
		font-size: 85%;
	}
}
@media screen and (max-width: 780px) {
	body {
		font-size: 65%;
	}
	.content {
		width: 100%;
	}
}
@media screen and (max-width: 760px) {
	.content-wrapper {
		padding: 0;
	}
}